import {Component, Input, OnInit} from '@angular/core';
import {ModalController, Platform, ToastController} from '@ionic/angular';
import {NGXLogger} from 'ngx-logger';
import {PatientProfileService, SurveyDetailsService} from '@clinician/providers';
import {Moment} from 'moment';
import {DailySurveyDetailsModel} from '@hrs/models';
import {TranslateService} from '@ngx-translate/core';
let moment = require('moment-timezone');

@Component({
    selector: 'page-patient-surveys',
    templateUrl: 'patient-surveys.page.html',
    styleUrls: ['patient-surveys.page.scss'],
})
export class PatientSurveysPage implements OnInit {
    @Input() hrsid: string;
    @Input() todaysSurvey: DailySurveyDetailsModel;
    public surveyDetails: DailySurveyDetailsModel[] = [];
    public currentMetricDay: number = 0;
    public loading: boolean;

    constructor(
        private platform: Platform,
        private logger: NGXLogger,
        private modalCtrl: ModalController,
        private surveyDetailsService: SurveyDetailsService,
        private patientProfileService: PatientProfileService,
        private toastCtrl: ToastController,
        private translateService: TranslateService,
    ) {}

    ngOnInit() {
        if (this.todaysSurvey) {
            this.surveyDetails.push(this.todaysSurvey);
        } else {
            this.getSurveyQuestionAndAnswerMetric(this.hrsid, moment(), false);
        }
    }

    /**
     * Get survey questions and answers
     */

    public getSurveyQuestionAndAnswerMetric(hrsid: string, date: any, increment?: boolean): void {
        this.loading = true;
        this.surveyDetailsService.getSurveyDetails(hrsid, date.format('YYYY-MM-DD')).subscribe(
            {
                next: (res: DailySurveyDetailsModel) => {
                    this.surveyDetails.push(res);
                    this.loading = false;
                },
                error: (err) => {
                    this.logger.error('Error: ', err);
                },
                complete: () => {
                    if (increment) {
                        this.currentMetricDay++;
                    }
                }
            }
        );
    }

    /**
     * Handle button click for Previous survey day
     */

    public showPreviousDay(): void {
        if (this.currentMetricDay > 0) {
            this.currentMetricDay--;
        }
    }

    /**
     * Handle button click for Next survey day
     */

    public showNextDay(): void {
        if ((this.currentMetricDay < 13) && !this.surveyDetails[this.currentMetricDay + 1]) {
            this.getSurveyQuestionAndAnswerMetric(this.hrsid, this.nextDate, true);
        } else {
            this.currentMetricDay++;
        }
    }

    /**
     * Get Moment object of Previous survey day
     */

    get previousDate(): Moment {
        if (this.currentMetricDay > 0) {
            return moment().subtract(this.currentMetricDay - 1, 'days');
        }
    }

    /**
     * Get Moment object of Next survey day
     */

    get nextDate(): Moment {
        if (this.currentMetricDay < 13) {
            return moment().subtract(this.currentMetricDay + 1, 'days');
        }
    }

    /**
     * Reset Patient survey answers
     */

    public resetSurveyAnswers(): void {
        this.patientProfileService.resetPatientMetrics(this.hrsid, {survey: true}).subscribe(
            {
                next: (res: any) => {
                    let message = this.translateService.instant('PATIENT_SURVEYS.SUCCESS');
                    this.throwToastAlert(message);
                },
                error: (err) => {
                    let message = this.translateService.instant('PATIENT_SURVEYS.FAIL');
                    this.throwToastAlert(message);
                }
            }
        );
    }

    async throwToastAlert(message: string) {
        let toast = await this.toastCtrl.create({
            message: message,
            duration: 3000,
            position: 'top'
        });
        return await toast.present();
    }

    /**
     * Get current formatted day
     */

    public getDayFromDate(date: Moment): string {
        if (date && date.isValid()) {
            return date.format('MM/DD/YYYY');
        } else {
            return '--';
        }
    }

    /**
     * Get labels for dates that are 'today' and 'yesterday'
     */

    public getDateLabel(date: Moment): string {
        let today = moment().format('MM/DD/YYYY');
        let yesterday = moment(today).subtract(1, 'days').format('MM/DD/YYYY');

        if (date && date.isValid()) {
            if (this.getDayFromDate(date) === today) {
                return this.translateService.instant('PATIENT_SURVEYS.TODAY').toUpperCase();
            } else if (this.getDayFromDate(date) === yesterday) {
                return this.translateService.instant('PATIENT_SURVEYS.YESTERDAY').toUpperCase();
            } else {
                return '';
            }
        }
    }
}
