import {Component, ElementRef, ViewChild} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {
    AlertController,
    IonContent,
    LoadingController,
    ModalController,
    NavController,
    ToastController
} from '@ionic/angular';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import {NGXLogger} from 'ngx-logger';
import {HttpClient} from '@angular/common/http';
import {Patient} from '@clinician/models';
import {DailySurveyDetailsModel} from '@hrs/models';

// observables
import {forkJoin, of} from 'rxjs';
import {catchError} from 'rxjs/operators';

// Modals
import {AddDevicePage} from './device/add-device.page';
import {ResetMetricsPage} from './reset-metrics/reset-metrics.page';
import {User} from '../services/user/user';
import {
    DataExchangeService,
    PatientDeviceService,
    PatientProfileService,
    ReviewedStatusService,
    SurveyDetailsService
} from '@clinician/providers';
import {ChatPage} from '../communication/chat/chat.page';
import {VideoPage} from '../communication/video/video.page';
import {VoicePage} from '../communication/voice/voice.page';
import {PatientStatusChange} from './patient-status-change/patient-status-change';
import {DisplayGenderPipe} from '../../pipes/display-gender.pipe';
import * as moment from 'moment';
import {DailyMetricsModel} from '../../common/models/daily-metrics.model';
import {CareplanPage} from './careplan/careplan.page';
import {ActivationStatus} from '../enums';

@Component({
    selector: 'page-patient-profile',
    templateUrl: 'patient-profile.page.html',
    styleUrls: ['patient-profile.page.scss'],
    providers: [NGXLogger, DisplayGenderPipe]
})
export class PatientProfilePage {
    @ViewChild(IonContent, null) content: IonContent;
    status = ActivationStatus;

    patient: Patient[];
    patientList: Patient[];
    Object = Object;
    device: any;
    deviceInformation: any;
    risk: any = {};
    hrsid: string;
    editedQuickNote: Boolean = false;
    surveyAnswers: number;
    cancelMessage: string;
    confirmMessage: string;
    confirmTitle: string;
    unassignedDevice: string;
    // The account fields for the quicknote form from HTML
    quickNote: FormGroup;
    mode: string;
    profileLoading: any;
    patientHasNoCarePlan: boolean;
    patientHasSurveyModule: boolean;
    loading: boolean;
    reviewedState: string;
    reviewedStates: string[];
    reviewedPosting: boolean = false;
    requestedState: string;
    public conditions: string[];
    public todaysMetrics: DailyMetricsModel[];
    public openSurveys: boolean = false; // toggle boolean from historical data component
    public hasTodaysMetrics: boolean = false;
    private surveyDetails: DailySurveyDetailsModel;

    constructor(
        private alertCtrl: AlertController,
        private displayGender: DisplayGenderPipe,
        private dataExchangeService: DataExchangeService,
        private formBuilder: FormBuilder,
        private http: HttpClient,
        private loadingCtrl: LoadingController,
        private logger: NGXLogger,
        private modalCtrl: ModalController,
        private myInput: ElementRef,
        private navCtrl: NavController,
        private patientDeviceService: PatientDeviceService,
        private patientProfileService: PatientProfileService,
        private route: ActivatedRoute,
        private surveyDetailsService: SurveyDetailsService,
        private toastCtrl: ToastController,
        private translateService: TranslateService,
        private user: User,
        private reviewedStatusService: ReviewedStatusService
    ) {
        this.quickNote = this.formBuilder.group({
            clinician: new FormControl(undefined, null),
            note: new FormControl('', null),
            time: new FormControl(undefined, null)
        });
    }

    ionViewWillEnter() {
        this.loading = true;
    }

    ngOnInit() {
        const patientList = [];
        this.route.queryParams.subscribe((params) => {
            patientList.push(params.patient);
            this.patientList = patientList;
            this.hrsid = params.patient.hrsid;
            this.surveyDetails = params.patient.surveyDetails;
            this.mode = params.mode;

            this.presentProfileLoading();
            this.loadPatientProfile(true);
            this.loading = true;
        });
        this.reviewedStates = [
            ReviewedStatusService.NON_REVIEWED,
            ReviewedStatusService.IN_PROGRESS,
            ReviewedStatusService.REVIEWED
        ];
    }

    async presentProfileLoading() {
        let loadingAlert = await this.loadingCtrl.create({
            message: this.translateService.instant('LOADING')
        });
        return await loadingAlert.present();
    }

    profileLoadingDismiss() {
        this.loadingCtrl.dismiss();
    }

    loadPatientProfile(loading?, refresher?) {
        const currentTime = new Date();
        const currentTimeReformatted = moment(currentTime).format('YYYY-MM-DD');
        let observable;
        if (this.surveyDetails) {
            observable = forkJoin(
                [this.patientProfileService.getPatientProfile(this.hrsid).pipe(catchError((error) => of(error))),
                    this.patientProfileService.getPatientCurrentMetrics(this.hrsid).pipe(catchError((error) => of(error)))]
            );
        } else {
            observable = forkJoin(
                [this.patientProfileService.getPatientProfile(this.hrsid).pipe(catchError((error) => of(error))),
                    this.patientProfileService.getPatientCurrentMetrics(this.hrsid).pipe(catchError((error) => of(error))),
                    this.surveyDetailsService.getSurveyDetails(this.hrsid, currentTimeReformatted).pipe(catchError((error) => of(error)))]
            );
        }
        observable.subscribe(([profileRes, metricsRes, surveyRes]: any) => {
            if (metricsRes[0] && metricsRes[0].discharged) {
                profileRes.discharged = metricsRes[0].discharged;
            }
            // parse profile res
            this.parseProfileDetails(profileRes);
            // parse metrics res
            this.parseMetricDetails(metricsRes);
            // parse surveys res
            if (surveyRes && !this.surveyDetails) {
                this.surveyAnswers = surveyRes.answeredCount;
                this.surveyDetails = surveyRes;
            }
            // if pulled to refresh, end the loading indicator
            if (loading) {
                this.loading = false;
                this.profileLoadingDismiss();
            }
            if (refresher) {
                refresher.target.complete();
            }
        });
    }

    parseProfileDetails(profileRes) {
        const patient = [];

        patient.push(new Patient(profileRes));
        this.patient = patient;

        if (profileRes.quicknote) {
            let updatedQuickNotes = profileRes.quicknote;
            if (this.isBase64(updatedQuickNotes.note)) {
                updatedQuickNotes.note = atob(updatedQuickNotes.note);
                updatedQuickNotes.note = decodeURIComponent(updatedQuickNotes.note);
                this.quickNote.setValue(updatedQuickNotes, {emitEvent: true});
                this.editedQuickNote = true;
            }
        } else {
            this.quickNote = this.formBuilder.group({
                clinician: new FormControl(undefined, null),
                note: new FormControl('', null),
                time: new FormControl(undefined, null)
            });
        }
        if (profileRes.device) {
            this.device = profileRes.device;
            this.getPatientDeviceInformation(this.device.devid);
        } else {
            this.deviceInformation = [];
        }
        if (profileRes.conditions) this.conditions = profileRes.conditions;

        this.reviewedState = profileRes.reviewed && profileRes.reviewed[0] ? profileRes.reviewed[0].reviewedStatus : ReviewedStatusService.NON_REVIEWED;
        setTimeout(() => {
            this.styleActiveReviewedBtn(this.reviewedState);
        }, 0);
    }

    parseMetricDetails(metricsRes) {
        if (metricsRes && metricsRes.length !== 0) {
            this.todaysMetrics = {...metricsRes[0].metrics};
            this.hasTodaysMetrics = (Object.keys(this.todaysMetrics).length > 0);
            this.parsePatientRisk(metricsRes[0].risk);

            if (metricsRes[0].tracking && metricsRes[0].tracking.lastactive) {
                this.patient[0].lastactive = metricsRes[0].tracking.lastactive;
            } else {
                this.patient[0].lastactive = undefined;
            }

            if (metricsRes[0].metrics && Object.keys(metricsRes[0].metrics).includes('survey')) {
                this.patientHasSurveyModule = true;
            } else {
                this.patientHasSurveyModule = false;
            }

            if (!metricsRes[0].metrics || Object.keys(metricsRes[0].metrics).length === 0) {
                this.patientHasNoCarePlan = true;
            } else {
                this.patientHasNoCarePlan = false;
            }
        }

        if (this.surveyDetails) {
            this.surveyAnswers = this.surveyDetails.answeredCount;
        }
    }

    /**
     * Tests if Base65 decoder is needed
     */

    isBase64(str) {
        try {
            return btoa(atob(str)) == str;
        } catch (err) {
            return false;
        }
    }

    /**
     * Calling updated patient profile after an edit
     */

    getPatientProfile(hrsid: string): object {
        return this.patientProfileService.getPatientProfile(hrsid).subscribe(
            {
                next: (res: any) => {
                    this.parseProfileDetails(res);
                },
                error: (err) => {
                    this.logger.error('Error: ', err);
                }
            }
        );
    }

    /**
     * Conversion from epoch into readable format
     */

    convertTimeAndDate(time: number): string {
        const newTimeObject = moment(time).format('MM/DD/YYYY');
        return newTimeObject;
    }

    convertTimeAndDateWithMinutes(time: number): string {
        const newTimeObject = moment(time).format('MM/DD/YYYY h:mm:ss A');
        return newTimeObject;
    }

    /**
     * From patient endpoint, assessing if there is a device connection and making the device call for details
     */

    getPatientDeviceInformation(deviceId): object {
        return this.patientDeviceService.getPatientDeviceInformation(deviceId).subscribe(
            {
                next: (res: any) => {
                    const deviceInformation = [];
                    deviceInformation.push(res);
                    this.deviceInformation = deviceInformation;
                    this.deviceInformation[0].deviceDeleted = false;
                },
                error: (err) => {
                    this.logger.error('Error: ', err);
                }
            }
        );
    }

    lastReviewedText(lastReviewed): string {
        return this.translateService.instant('REVIEWED_STATUS_LAST_REVIEWED',
            {
                reviewedStatus: this.reviewedStatusService.translateReviewedStatus(lastReviewed.reviewedStatus),
                clinicianName: lastReviewed.name,
                date: moment(parseInt(lastReviewed.time)).format('MM/DD/YYYY'),
                time: moment(parseInt(lastReviewed.time)).format('h:mm:ss A')
            }
        );
    }

    /**
     * Sending new reviewed status and updating local patient on success
     */
    async submitNewReviewedStatus(requestedStatus: string) {
        this.requestedState = requestedStatus;
        this.reviewedPosting = true;
        return this.reviewedStatusService.submitRequestedStatus(this.patient[0].hrsid, requestedStatus)
            .subscribe(
                {
                    next: (res) => {
                        this.reviewedState = requestedStatus;
                        this.patient[0].reviewedState = requestedStatus;
                        const reviewedStatus = [{name: this.user.getClinicianName(), time: Date.now().toString(), reviewedStatus: requestedStatus}];
                        this.patient[0].reviewed = reviewedStatus;
                        this.patient[0]['lastreviewed'] = requestedStatus !== ReviewedStatusService.NON_REVIEWED ? reviewedStatus : undefined;
                        this.reviewedPosting = false;
                        this.requestedState = undefined;
                        this.dataExchangeService.setData(DataExchangeService.UpdatedReviewedStatus, {hrsid: this.hrsid, reviewedStatus: reviewedStatus});
                        this.styleActiveReviewedBtn(requestedStatus);
                    },
                    error: () => {
                        this.reviewedPosting = false;
                        this.requestedState = undefined;
                    }
                }
            );
    }

    private styleActiveReviewedBtn(requestedStatus): void {
        // iOS only: although the computed styles in web inspector are showing the proper text color values for the reviewed buttons,
        // the white text color from the active button styling sticks even after the button is not active anymore.
        // The root of the issue is unknown, but this is to prevent the unselected review status buttons from looking empty.
        let activeBtn;
        switch (requestedStatus) {
            case 'unreviewed':
                activeBtn = 0;
                break;
            case 'inprogress':
                activeBtn = 1;
                break;
            case 'reviewed':
                activeBtn = 2;
        }

        const buttons: any = document.querySelectorAll('.c_patient_profile--reviewed_buttons ion-button');
        buttons.forEach((btn: HTMLElement, i) => {
            setTimeout(() => {
                if (i === activeBtn) {
                    btn.style.color = 'white';
                } else {
                    btn.style.color = 'black';
                }
            }, 0);
        });
    }

    /**
     * Save and updates quicknotes after editing
     */

    saveQuickNote(detail?) {
        return this.patientProfileService.saveQuickNote(detail, this.quickNote.value.note).subscribe(
            {
                next: (res: any) => {
                    if (this.quickNote.value.clinician == undefined || !detail.quicknote) {
                        this.editedQuickNote = true;
                        this.quickNote.value.clinician = res.quicknote.clinician.first + ' ' + res.quicknote.clinician.last;
                        this.quickNote.value.time = res.quicknote.lastUpdated;
                        detail.quicknote = this.quickNote.value;
                    } else {
                        detail.quicknote.clinician = res.quicknote.clinician.first + ' ' + res.quicknote.clinician.last;
                        detail.quicknote.time = res.quicknote.lastUpdated;
                    }
                },
                error: (err) => {
                    this.logger.error('Error: ', err);
                }
            }
        );
    }

    /**
     * Open modal to choose or edit the device for that patient
     */

    async removeConnectedDeviceModal(detail) {
        this.translateService.get('CANCEL_BUTTON').subscribe((value) => {
            this.cancelMessage = value;
        });
        this.translateService.get('CONFIRM_BUTTON').subscribe((value) => {
            this.confirmTitle = value;
        });
        this.translateService.get('CONFIRM_MESSAGE').subscribe((value) => {
            this.confirmMessage = value;
        });

        let alertOptions = {
            message: this.confirmMessage,
            title: this.confirmTitle,
            buttons: [
                {
                    text: this.cancelMessage,
                    role: 'cancel',
                    handler: () => {
                        this.logger.debug('Cancel clicked');
                    }
                },
                {
                    text: this.confirmTitle,
                    handler: () => {
                        this.logger.debug('Confirm deletion');
                        this.removeDevice(detail);
                    }
                }
            ]
        };

        let alert = await this.alertCtrl.create(alertOptions);
        return await alert.present();
    }

    /**
     * Remove device from patient and reset to nothing assigned
     */

    removeDevice(detail) {
        return this.patientDeviceService.removeDevice(detail).subscribe(
            {
                next: (res: any) => {
                    this.logger.debug(res);
                    if (!this.deviceInformation) {
                        this.getPatientProfile(detail.hrsid);
                        this.patientList[0].device = false;
                        this.deviceInformation = [{'deviceDeleted': true}];
                    } else {
                        this.deviceInformation.deviceDeleted = true;
                        this.patientList[0].device = false;
                        this.getPatientProfile(detail.hrsid);
                    }
                },
                error: (err) => {
                    this.logger.error('Error: ', err);
                }
            }
        );
    }

    /**
     * Launch the care plan modal page
     */
    async openCarePlanModal(patient) {
        const modal = await this.modalCtrl.create({
            component: CareplanPage,
            componentProps: {
                patient: patient,
                activeModules: Object.keys(this.todaysMetrics),
                activePresets: patient.conditions
            }
        });

        // Getting data from the modal on dismiss
        modal.onDidDismiss().then(async (data) => {
            if (data == undefined) {
                this.logger.debug(data.data, 'data is undefined');
            } else {
                this.updateCarePlanValues(data.data);
                if (data.data.hasChanges) {
                    this.notifyCareplanSuccess();
                    this.patientHasNoCarePlan = false;
                }
            }
        });
        await modal.present();
    }

    updateCarePlanValues(updatedSettings) {
        forkJoin(
            [this.patientProfileService.getPatientProfile(this.hrsid).pipe(catchError((error) => of(error))),
                this.patientProfileService.getPatientCurrentMetrics(this.hrsid).pipe(catchError((error) => of(error)))]
        ).subscribe(([profileRes, metricsRes]: any) => {
            this.parseProfileDetails(profileRes);
            this.parseMetricDetails(metricsRes);

            this.dataExchangeService.setData(DataExchangeService.UpdatePatient, {
                hrsid: this.hrsid,
                conditions: profileRes.conditions,
                metrics: metricsRes[0].metrics
            });
        });
    }

    async notifyCareplanSuccess() {
        let careplanSavedMessage;
        let careplanSavedTitle;
        this.translateService.get('PATIENT_PROFILE_CAREPLAN_SUCCESS_HEADER').subscribe((value) => {
            careplanSavedMessage = value;
        });
        this.translateService.get('PATIENT_PROFILE_CAREPLAN_SUCCESS_MESSAGE').subscribe((value) => {
            careplanSavedTitle = value;
        });

        const toast = await this.toastCtrl.create({
            header: careplanSavedMessage,
            message: careplanSavedTitle,
            cssClass: 'toast-success',
            duration: 3000,
            position: 'top',
        });

        await toast.present();
    }

    /**
     * Launch the edit device modal page
     */
    async editConnectedDeviceModal(patient) {
        this.translateService.get('UNASSIGNED_DEVICE').subscribe((value) => {
            this.unassignedDevice = value;
        });
        let device;
        if (patient.device && patient.device.name) {
            device = patient.device.name;
        } else {
            device = this.unassignedDevice;
        }
        const modal = await this.modalCtrl.create({
            component: AddDevicePage,
            componentProps: {device: device, patient: patient.hrsid}
        });

        // Getting data from the modal on dismiss
        modal.onDidDismiss().then((data) => {
            if (data == undefined) {
                this.logger.debug(data.data, 'data is undefined');
            } else {
                this.getPatientDeviceInformation(data.data);
                this.updatePatientDeviceList(data);
            }
        });
        await modal.present();
        // creates the modal
    }

    updatePatientDeviceList(data): void {
        this.patient[0]['device'] = data.data;
        this.patientList[0].device = data.data;
    }

    /**
     * Launch the chat messages modal
     */

    async openChatMessages(patient) {
        let modal = await this.modalCtrl.create({
            component: ChatPage,
            componentProps: {patient: patient}
        });
        // Getting data from the modal on dismiss
        // modal.onDidDismiss(data => {});
        return await modal.present();
        // creates the modal
    }

    /**
     * Launch the video call modal
     */

    async openVideoCall(patient) {
        let patientName = patient.name.first + ' ' + patient.name.last;
        const modal = await this.modalCtrl.create({
            component: VideoPage,
            componentProps: {
                callData: {
                    patientName: patientName,
                    patientHrsId: patient.hrsid
                }
            }});
        return await modal.present();
        // creates the modal
    }

    /**
     * Launch the voice call modal
     */

    async openVoiceCall(patient) {
        let patientName: string = patient.name.first + ' ' + patient.name.last;
        let riskClass: string;
        if (this.risk && this.risk.max) {
            riskClass = this.risk.max;
        } else {
            riskClass = ActivationStatus.StatusPreActivated;
        }
        const modal = await this.modalCtrl.create({
            component: VoicePage,
            componentProps: {
                callData: {
                    patientName: patientName,
                    patientHrsId: patient.hrsid,
                    patientGender: this.displayGender.transform(patient.gender),
                    riskClass: riskClass
                }
            }
        });
        return await modal.present();
    }

    /**
     *  Gets patient overall risk for profile pic coloring
     */
    getPatientOverallRiskClass(todaysRisk) {
        if (
            typeof todaysRisk.value !== 'undefined'
        ) {
            let riskLevel = todaysRisk.value;
            if (riskLevel === 4 || riskLevel === 5) {
                return 'highRisk';
            } else if (riskLevel === 2 || riskLevel === 3) {
                return 'medRisk';
            } else {
                return 'lowRisk';
            }
        } else {
            return ActivationStatus.StatusPreActivated;
        }
    }

    /**
     *  sets classes for each metrics risk level
     */
    getMetricRiskClasses(todaysRisk) {
        const metricClasses = {};
        if (todaysRisk.details) {
            let riskDetails = todaysRisk.details;
            for (let i = 0; i < riskDetails.length; i++) {
                let metricType = riskDetails[i].metrics[0];
                metricClasses[metricType + 'Risk'] = riskDetails[i].type;
            }
        }
        return metricClasses;
    }

    /**
     * Parses patient risk levels for risk coloring
     */
    parsePatientRisk(risk) {
        if (
            risk &&
            risk[0] &&
            this.patient &&
            this.patient[0].status === ActivationStatus.StatusActivated
        ) {
            let todaysRisk = risk[0];
            this.risk.max = this.getPatientOverallRiskClass(todaysRisk);
            this.risk.classes = this.getMetricRiskClasses(todaysRisk);
        }
    }

    async resetMetrics() {
        let resetMetricsModal = await this.modalCtrl.create({
            component: ResetMetricsPage,
            componentProps: {patientHRSId: this.hrsid}
        });
        return await resetMetricsModal.present();
    }

    doRefresh(refresher) {
        // this ensures we pull any new survey details on refresh
        this.surveyDetails = undefined;
        this.loadPatientProfile(false, refresher);
    }

    canContactPatient() {
        return this.user.canContactPatient();
    }

    canEditPatientInfo() {
        return this.user.canEditPatientInfo();
    }

    async changePatientStatus(clickedButton: string): Promise<any> {
        let action;
        switch (clickedButton) {
            case ActivationStatus.ActionPause:
                action = (this.patient[0].status == ActivationStatus.StatusPaused) ? ActivationStatus.ActionResume : ActivationStatus.ActionPause;
                break;
            case ActivationStatus.ActionDeactivate:
                action = (this.patient[0].status == ActivationStatus.StatusDeactivated) ? ActivationStatus.ActionReactivate : ActivationStatus.ActionDeactivate;
                break;
            case ActivationStatus.ActionDischarge:
                action = (this.patient[0].status == ActivationStatus.StatusDischarged) ? ActivationStatus.ActionUnDischarge : ActivationStatus.ActionDischarge;
                break;
        }

        const modal = await this.modalCtrl.create({
            component: PatientStatusChange,
            componentProps: {hrsid: this.patient[0].hrsid, action: action, date: this.patient[0].discharged}
        });
        // Getting data from the modal on dismiss
        modal.onDidDismiss().then((data: {data: {[key: string]: string}, role: undefined}) => {
            if (data.data === undefined) {
                this.logger.debug(data, 'data is undefined');
            } else {
                if (action === ActivationStatus.ActionPause) {
                    this.patient[0].status = ActivationStatus.StatusPaused;
                } else if (action === ActivationStatus.ActionReactivate) {
                    this.patient[0].status = ActivationStatus.StatusPreActivated;
                } else if (action === ActivationStatus.ActionDeactivate) {
                    this.patient[0].status = ActivationStatus.StatusDeactivated;
                } else if (action === ActivationStatus.ActionDischarge) {
                    this.patient[0].status = ActivationStatus.StatusDischarged;
                    this.patient[0].discharged = data.data.date;
                } else if (action === ActivationStatus.ActionUnDischarge || action === ActivationStatus.ActionResume) {
                    this.patient[0].status = ActivationStatus.StatusActivated;
                }

                this.dataExchangeService.setData(DataExchangeService.UpdatedActivationStatus, {
                    hrsid: this.patient[0].hrsid,
                    status: this.patient[0].status,
                    discharged: this.patient[0].status === ActivationStatus.StatusDischarged ? this.patient[0].discharged : null
                });
            }
        });
        await modal.present();
    }

    /**
     * Handles event emitted from Historical Data component on click of Surveys button
     */

    public toggleSurveysCard(event: boolean): void {
        if (!event) {
            this.scrollTo('metrics');
            setTimeout(() => {
                this.openSurveys = event;
            }, 1000);
        } else {
            this.openSurveys = event;
            setTimeout(() => {
                this.scrollTo('surveys-card');
            }, 10);
        }
    }

    /**
     * Scrolls to and from Surveys component
     */

    public scrollTo(elementId: string): void {
        let yOffset = document.getElementById(elementId).offsetTop;
        this.content.scrollToPoint(0, yOffset, 900);
    }
}
