import {TranslateService} from '@ngx-translate/core';

export class MetricDetailMap {
    public activity: string[];
    public bloodpressure: string[];
    public glucose: string[];
    public pulseox: string[];
    public temperature: string[];
    public weight: string[];
    public medication: string[];
    public woundimaging: string[];
    public stethoscope: string[];
    public steps: string[];

    constructor(
        private translateService: TranslateService
    ) {
        this.activity = [this.translateService.instant('METRIC_DETAIL_MAP.DURATION')];
        this.bloodpressure = [this.translateService.instant('METRIC_DETAIL_MAP.SYSTOLIC'), this.translateService.instant('METRIC_DETAIL_MAP.DIASTOLIC'), this.translateService.instant('METRIC_DETAIL_MAP.HEARTRATE')];
        this.glucose = [this.translateService.instant('METRIC_DETAIL_MAP.GLUCOSE')];
        this.pulseox = [this.translateService.instant('METRIC_DETAIL_MAP.SPO2'), this.translateService.instant('METRIC_DETAIL_MAP.HEARTRATE')];
        this.temperature = [this.translateService.instant('METRIC_DETAIL_MAP.TEMPERATURE')];
        this.weight = [this.translateService.instant('METRIC_DETAIL_MAP.WEIGHT')];
        this.medication = [];
        this.woundimaging = [];
        this.stethoscope = [];
        this.steps = [this.translateService.instant('METRIC_DETAIL_MAP.STEPS')];
    }
}
