import {TranslateService} from '@ngx-translate/core';

export class MetricUnitMap {
    public activity: UnitDetails;
    public bloodpressure: UnitDetails;
    public glucose: UnitDetails;
    public pulseox: UnitDetails;
    public temperature: UnitDetails;
    public weight: UnitDetails;
    public stethoscope: UnitDetails;
    public survey: UnitDetails;
    public medication: UnitDetails;
    public woundimaging: UnitDetails;
    public steps: UnitDetails;

    constructor(
        private translateService: TranslateService
    ) {
        this.activity = {
            closed: this.translateService.instant('METRIC_UNIT_MAP.MIN'),
            open: this.translateService.instant('METRIC_UNIT_MAP.MIN')
        };
        this.bloodpressure = {
            closed: '',
            open: 'mmHg/mmHg/bpm'
        };
        this.glucose = {
            closed: '',
            open: 'mg/dL'
        };
        this.pulseox = {
            closed: '',
            open: 'SpO2%/bpm'
        };
        this.temperature = {
            closed: 'F',
            open: 'F'
        };
        this.weight = {
            closed: this.translateService.instant('METRIC_UNIT_MAP.LBS'),
            open: this.translateService.instant('METRIC_UNIT_MAP.LBS')
        };
        this.stethoscope = {closed: '', open: this.translateService.instant('METRIC_UNIT_MAP.RECORDINGS')};
        this.survey = {closed: this.translateService.instant('METRIC_UNIT_MAP.ANSWERS')};
        this.medication = {open: this.translateService.instant('METRIC_UNIT_MAP.INTAKES')};
        this.woundimaging = {open: this.translateService.instant('METRIC_UNIT_MAP.IMAGES')};
        this.steps = {closed: '', open: this.translateService.instant('METRIC_UNIT_MAP.STEPS')};
    }
}

interface UnitDetails {
    closed?: string,
    open?: string
}
