import {Component, Input, Output, OnChanges, EventEmitter} from '@angular/core';
import {Platform} from '@ionic/angular';
import {TranslateService} from '@ngx-translate/core';
import {HttpClient} from '@angular/common/http';
import {Moment} from 'moment';
import {NGXLogger} from 'ngx-logger';
import {MetricDetailMap} from './metric-detail-map';
import {MetricUnitMap} from './metric-unit-map';
import {DailyMetricsModel} from '../../../common/models/daily-metrics.model';
import {PatientRiskResponse, PatientMetricResponse} from '../../../common/interfaces/interfaces';
import {HistoricalDisplayData, MetricData} from './historical-display-data.interface';
import {HistoricalDataService} from '@clinician/providers';
import * as moment from 'moment-timezone';
import {DailySurveyDetailsModel} from '@hrs/models';
import {MetricType} from '../../../common/models/daily-metrics.model';

@Component({
    selector: 'historical-data-page',
    templateUrl: 'historical-data.page.html',
    styleUrls: ['historical-data.page.scss'],
})
export class HistoricalDataPage implements OnChanges {
    @Input('hrsid') hrsid: string;
    @Input('todaysMetrics') todaysMetrics: PatientMetricResponse;
    @Input('risk') risk: PatientRiskResponse;
    @Input() patientHasNoCarePlan: boolean;
    @Input() patientHasSurveyModule: boolean;
    @Input('surveyDetails') surveyDetails: DailySurveyDetailsModel;
    @Output() toggleSurveysCard: EventEmitter<any> = new EventEmitter<any>();

    public expandedMetrics: string[] = [];
    public historicalData: HistoricalDisplayData = {};
    public metricDetailMap: MetricDetailMap; // matches metrics to their detail names
    public metricUnitMap: MetricUnitMap; // matches metrics to their units of measurement
    public medications: string;
    public noRecordedMetrics: boolean = false;
    private hasLessThan7: boolean = false;

    constructor(
        private platform: Platform,
        private http: HttpClient,
        private translateService: TranslateService,
        private historicalDataService: HistoricalDataService,
        private logger: NGXLogger,
    ) {
        this.metricDetailMap = new MetricDetailMap(this.translateService);
        this.metricUnitMap = new MetricUnitMap(this.translateService);
    }

    ngOnChanges() {
        if (this.todaysMetrics && !this.medications) {
            this.calculateCurrentMedicationMetric(this.todaysMetrics);
        }

        // remove when patientconnectvoice is fully implemented and has its own display card
        if (this.todaysMetrics) {
            const metricKeys = Object.keys(this.todaysMetrics);
            if (metricKeys.length === 1 && metricKeys[0] === 'patientconnectvoice') {
                this.noRecordedMetrics = true;
            } else {
                this.noRecordedMetrics = false;
            }
        }
    }

    /**
     * Expand items on the patient list
     */

    public toggleMetricExpanded(metric: string): void {
        if (this.expandedMetrics.includes(metric)) {
            this.expandedMetrics = this.expandedMetrics.filter((expandedMetric) => {
                return expandedMetric !== metric;
            });
            this.historicalData[metric].numberOfDays = null;
        } else {
            this.expandedMetrics.push(metric);
            if (this.historicalData[metric] && this.historicalData[metric].data.length >= 7) {
                this.historicalData[metric].numberOfDays = 7;
            } else {
                this.getHistoricalData(metric);
                this.historicalData[metric] = {data: [], isLoading: true, numberOfDays: 7};
            }
        }
    }

    /**
     * Button click handler to show more Historical Data
     */

    public showMoreHistoricalData(metric) {
        if (this.historicalData[metric].data.length < 14) {
            this.getHistoricalData(metric);
        }
        this.historicalData[metric].numberOfDays = 14;
    }

    public calculateHeightOfExpandedMetric(metric: string): number {
        let entries = 0;
        let days = 0;
        // 230px is the expected height for an expanded metric container that has either no readings or just one
        // reading per day. Since medication only uses one line per day, return early here
        if (metric === 'medication') return 230;
        this.historicalData[metric].data.slice(0, this.historicalData[metric].numberOfDays).forEach((day) => {
            if (day[metric]) {
                entries += day[metric].length;
                days += 1;
            }
        });
        // Each entry in a row with multiple readings is roughly 16px tall, while each row with one or no readings
        // is 20px tall. The metric label button on top of the table and the "show more days" button are both ~45px tall.
        // Here we calculate the height needed for the .metrics_list--row_container div once it opens by multiplying the
        // total number of entries by 16, then accounting for rows that will only have one or no readings,
        // and finally adding the fixed combined heights of the top and bottom buttons
        if (this.hasLessThan7) {
            return (16 * entries) + (20 * (7 - days)) + 45;
        } else {
            return (16 * entries) + (20 * (7 - days)) + 90;
        }
    }

    /**
     * Request Historical Data from Service
     */

    public getHistoricalData(metric: string): void {
        if (!this.historicalData[metric] || this.historicalData[metric].data.length < 1) {
            // create first metric so we can show loading spinner
            this.historicalDataService.getLatest7Days(this.hrsid).subscribe(
                {
                    next: (res) => {
                        this.formatHistoricalData(res);
                        this.historicalData[metric].isLoading = false;
                        if (res.length < 7) {
                            this.hasLessThan7 = true;
                        }
                    },
                    error: (err) => {
                        this.logger.error('Failed to get 7 Days of Historical Data');
                        this.expandedMetrics = [];
                        this.historicalData[metric].isLoading = false;
                    }
                }
            );
        } else if (this.historicalData[metric].data.length === 7) {
            this.historicalData[metric].isLoading = true;
            this.historicalDataService.getNext7Days(this.hrsid).subscribe(
                {
                    next: (res) => {
                        this.formatHistoricalData(res);
                        this.historicalData[metric].isLoading = false;
                    },
                    error: (err) => {
                        this.logger.error('Failed to get 14 Days of Historical Data');
                        this.expandedMetrics = [];
                        this.historicalData[metric].isLoading = false;
                    }
                }
            );
        }
    }

    private formatHistoricalData(res: DailyMetricsModel[]): void {
        for (let metric of this.metricValues) {
            if (!this.historicalData[metric]) {
                this.historicalData[metric] = {data: [], isLoading: false, numberOfDays: null};
            }

            for (let i = 0; i < 7; i++) {
                if (res[i]) {
                    let dailyModel = res[i];
                    if (metric === 'medication') {
                        this.historicalData[metric].data.push({
                            [metric]: dailyModel[metric],
                            date: dailyModel.date,
                            riskClasses: dailyModel.riskClasses,
                            medsTakenTotal: dailyModel.medsTakenTotal
                        });
                    } else {
                        this.historicalData[metric].data.push({[metric]: dailyModel[metric], riskClasses: dailyModel.riskClasses, date: dailyModel.date});
                    }
                } else {
                    // create empty 'days' to fill the 7 day view
                    this.historicalData[metric].data.push({
                        [metric]: [],
                        date: null,
                        riskClasses: {}
                    });
                }
            }
            if (metric === MetricType.STETHOSCOPE && this.historicalData[metric].data) {
                this.historicalData[metric].data = this.sortByDate(this.historicalData[metric].data);
            }
        }
    }

    /**
     * a recorded date with the UTC offset may place the recording on the next day
     * this is to correct for that
     */
    private sortByDate(data: MetricData[]): MetricData[] {
        let byDate = {};
        let recordings = [];
        for (let day of data) {
            // day.date returns null sometimes causing console errors. Added if statement to avoid errors.
            if (day.date) byDate[day.date.format('MM/DD/YYYY')] = {date: day.date, stethoscope: []};
            // day.stethoscope s/b an array, but sometimes is coming in as undefined and causing this to break since undefined isn't an iterable.
            // Thus, adding if statement to check to make sure day.stethoscope exists.
            if (day.stethoscope) recordings = [...recordings, ...day.stethoscope];
        }
        recordings.sort((a, b) => b.date.unix() - a.date.unix());
        for (let recording of recordings) {
            const date = recording.date.format('MM/DD/YYYY');
            if (byDate[date]) byDate[date].stethoscope.push(recording);
        }
        for (let day of data) {
            if (day.date) { // day.date returns null sometimes causing console errors. Added if statement to avoid errors.
                const date = day.date.format('MM/DD/YYYY');
                if (day.date === byDate[date].date) day.stethoscope = [...byDate[date].stethoscope];
            }
        }
        return data;
    }

    /**
     * Get formatted time for metric readings
     */

    public getTimeFromDate(date: Moment): string {
        if (date && date.isValid()) {
            return date.format('h:mm:ss A');
        } else {
            return '--';
        }
    }

    /**
     * Get formatted dates for each patient day
     */

    public getDayFromDate(date): string {
        // hardcoding the timezone to 'America/New_York' because the unix timestamps for each patient 'day' is 11:59PM EST
        if (date && date.isValid()) {
            return date.tz('America/New_York').format('MM/DD/YYYY');
        } else {
            return '--';
        }
    }

    /**
     * Get labels for dates that are 'today' and 'yesterday'
     */

    public getDateLabel(date: Moment): string {
        let today = moment().format('MM/DD/YYYY');
        let yesterday = moment().subtract(1, 'days').format('MM/DD/YYYY');

        if (date && date.isValid()) {
            if (this.getDayFromDate(date) === today) {
                return '(' + this.translateService.instant('HISTORICAL_DATA.LABEL.TODAY') + ')';
            } else {
                return '';
            }
        }
    }

    get metricValues(): string[] {
        return Object.keys(this.metricDetailMap).filter((metric) => Object.keys(this.todaysMetrics).includes(metric));
    }

    /**
     * Medication calculation
     */

    public calculateCurrentMedicationMetric(currentPatientMetrics: PatientMetricResponse): void {
        if (!currentPatientMetrics) {
            this.medications = '0/0';
        } else {
            let medications;
            let totalMedications = 0;
            const medicationsTakenToday = [];
            if (currentPatientMetrics.medication && currentPatientMetrics.medication.medications) {
                medications = currentPatientMetrics.medication.medications;
                totalMedications = medications.length;
                for (let i = 0; i < medications.length; i++) {
                    if (medications[i].ts) {
                        medicationsTakenToday.push(medications[i].ts);
                    }
                }

                this.medications = medicationsTakenToday.length + '/' + totalMedications;
            } else {
                this.medications = '0/0';
            }
        }
    }

    /**
     * Survey Button Event Emitter
     */

    public surveyButtonHandler(): void {
        this.toggleSurveysCard.emit(true);
    }
}
